
.topContent {
  background-color: rgba(7,17,27,0.5);
  width: 100%;
  position: relative;
}
.topContent .bgcolor {
  width: 100%;
  height: 100%;
  z-index: -1;
  position: absolute;
  -webkit-filter: blur(10px);
  -moz-filter: blur(10px);
  -ms-filter: blur(10px);
  filter: blur(10px);
}
.topContent .left {
  position: absolute;
  width: 88px;
  height: 106px;
}
.topContent .left .logo {
  border-radius: 4px;
  margin-left: 24px;
  margin-top: 24px;
  width: 64px;
  height: 64px;
}
.topContent .right {
  height: 106px;
  width: 100%;
  padding-left: 104px;
  box-sizing: border-box;
  position: relative;
}
.topContent .right .logoImg {
  display: inline-block;
  width: 36px;
  height: 18px;
  background: url("./brand@2x.png");
  background-size: 100%;
}
.topContent .right h2 {
  display: inline-block;
  box-sizing: border-box;
  height: 18px;
  margin-top: 24px;
  line-height: 18px;
  font-size: 16px;
  color: #fff;
  font-weight: bold;
  overflow: hidden;
}
.topContent .right p {
  overflow: hidden;
  color: #fff;
  margin-top: 8px;
  font-weight: 200px;
  font-size: 12px;
  line-height: 12px;
}
.topContent .right .tips {
  overflow: hidden;
  margin-top: 10px;
  overflow: hidden;
}
.topContent .right .tips .tipsImg {
  display: inline-block;
  width: 12px;
  height: 12px;
  vertical-align: baseline;
}
.topContent .right .tips .tipsImg.decrease {
  background: url("./decrease_1@2x.png");
  background-size: contain;
}
.topContent .right .tips .tipsImg.discount {
  background: url("./discount_1@2x.png");
  background-size: contain;
}
.topContent .right .tips .tipsImg.guarantee {
  background: url("./guarantee_1@2x.png");
  background-size: contain;
}
.topContent .right .tips .tipsImg.invoice {
  background: url("./invoice_1@2x.png");
  background-size: contain;
}
.topContent .right .tips .tipsImg.special {
  background: url("./special_1@2x.png");
  background-size: contain;
}
.topContent .right .tips p {
  overflow: hidden;
  height: 12px;
  margin-top: 0px;
  display: inline-block;
  color: #fff;
  font-weight: 200px;
  line-height: 12px;
  font-size: 12px;
}
.topContent .right .cover {
  overflow: hidden;
  position: absolute;
  bottom: 18px;
  right: 12px;
  display: inline-block;
  height: 24px;
  width: 38px;
  border-radius: 16px;
  background-color: rgba(0,0,0,0.2);
}
.topContent .right .cover p {
  display: inline-block;
  font-size: 10px;
  color: #fff;
  font-weight: 200px;
  line-height: 12px;
  margin-top: 6px;
  margin-left: 5px;
}
.topContent .right .cover::after {
  content: '>';
  color: #fff;
  font-weight: 200px;
  line-height: 12px;
  font-family: Consolas;
  padding-left: 2px;
}
.topContent .bottom {
  height: 28px;
  width: 100%;
  background-color: rgba(7,17,27,0.2);
}
.topContent .bottom::before {
  content: '';
  width: 22px;
  height: 12px;
  background: url("./bulletin@2x.png");
  background-size: 100%;
  position: absolute;
  left: 12px;
  bottom: 9px;
}
.topContent .bottom p {
  overflow: hidden;
  width: 100%;
  font-size: 10px;
  color: #fff;
  font-weight: 200;
  line-height: 28px;
  padding-left: 38px;
  padding-right: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  box-sizing: border-box;
}
.topContent .bottom::after {
  content: '>';
  width: 22px;
  height: 12px;
  position: absolute;
  right: -6px;
  bottom: 8px;
  font-size: 10px;
  color: #fff;
  font-weight: 200px;
  line-height: 12px;
  font-family: Consolas;
}
.topContent .transition {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.8);
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.topContent .transition .title {
  margin-top: 64px;
  text-align: center;
  color: #fff;
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  margin-bottom: 16px;
}
.topContent .transition .specialOffers {
  margin: 28px 36px;
  position: relative;
}
.topContent .transition .specialOffers .information {
  height: 14px;
  width: 100%;
  display: inline-block;
  text-align: center;
  font-size: 14px;
  font-weight: 700;
  color: #fff;
  line-height: 14px;
}
.topContent .transition .specialOffers .linelr-color {
  float: left;
  border-top: 1px solid rgba(255,255,255,0.2);
  width: 112px;
  display: inline-block;
  margin-top: -10px;
}
.topContent .transition .specialOffers .linerl-color {
  float: right;
  border-top: 1px solid rgba(255,255,255,0.2);
  width: 112px;
  display: inline-block;
  margin-top: -10px;
}
.topContent .transition .specialOffers .supportsList {
  margin-top: 24px;
  font-size: 12px;
  font-weight: 200;
  color: #fff;
  line-height: 12px;
  text-align: left;
}
.topContent .transition .specialOffers .supportsList li {
  margin-bottom: 13px;
  margin-left: 12px;
}
.topContent .transition .specialOffers .supportsList li .icons {
  display: inline-block;
  width: 18px;
  height: 18px;
  margin-right: 6px;
  vertical-align: middle;
}
.topContent .transition .specialOffers .supportsList li .icons.decrease {
  background: url("./decrease_2@2x.png");
  background-size: contain;
}
.topContent .transition .specialOffers .supportsList li .icons.discount {
  background: url("./discount_2@2x.png");
  background-size: contain;
}
.topContent .transition .specialOffers .supportsList li .icons.guarantee {
  background: url("./guarantee_2@2x.png");
  background-size: contain;
}
.topContent .transition .specialOffers .supportsList li .icons.invoice {
  background: url("./invoice_2@2x.png");
  background-size: contain;
}
.topContent .transition .specialOffers .supportsList li .icons.special {
  background: url("./special_2@2x.png");
  background-size: contain;
}
.topContent .transition .announcement {
  font-size: 12px;
  font-weight: 200;
  color: #fff;
  line-height: 24px;
  margin: 24px 12px 0;
}
.topContent .transition .closeShow {
  display: inline-block;
  position: absolute;
  width: 100%;
  height: 36px;
  bottom: 32px;
  color: rgba(255,255,255,0.5);
  font-size: 32px;
  text-align: center;
  padding: 2px 0;
  box-sizing: border-box;
}
.topContent .transition .closeShow:before {
  border: 2px solid rgba(255,255,255,0.5);
  border-radius: 50%;
}
.topContent .transition.fade-enter-active,
.topContent .transition.fade-leave-active {
  transition: opacity 0.8s;
}
.topContent .transition.fade-enter,
.topContent .transition.fade-leave-active {
  opacity: 0;
}


