html #app,
body #app {
  width: 100%;
  height: 100%;
/* Retina 适配 */
/* 三倍屏 适配 */
}
html #app .tab,
body #app .tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 28px;
  text-align: center;
  line-height: 28px;
  font-size: 14px;
  color: #4d555d;
  background-color: #fff;
  box-shadow: 0 1px 1px rgba(17,27,220,0.1);
}
html #app .tab .tab_item,
body #app .tab .tab_item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
html #app .tab .tab_item a,
body #app .tab .tab_item a {
  text-decoration: none;
  font-size: 14px;
  line-height: 14px;
  color: #4d555d;
}
html #app .tab .tab_item .router-link-exact-active,
