
.starScore {
  height: 22px;
  width: 100%;
  display: inline-block;
  text-align: center;
}
.starScore .startstar {
  display: inline-block;
  background-repeat: no-repeat;
}
.starScore.star-48 .startstar {
  width: 20px;
  height: 20px;
  margin-right: 22px;
}
.starScore.star-48 .startstar:last-child {
  margin-right: 0;
}
.starScore.star-48 .startstar.on {
  background: url("./star48_on@2x.png");
  background-size: 100% 100%;
}
.starScore.star-48 .startstar.half {
  background: url("./star48_half@2x.png");
  background-size: 100% 100%;
}
.starScore.star-48 .startstar.off {
  background: url("./star48_off@2x.png");
  background-size: 100% 100%;
}
