
#cart {
  height: 44px;
  width: 100%;
  background-color: #141d27;
  position: fixed;
  z-index: 999;
  bottom: 0px;
}
#cart .cartLeft {
  display: inline-block;
  position: fixed;
  left: 0px;
  bottom: 0px;
  width: 58px;
  height: 58px;
  margin-left: 12px;
  margin-bottom: 0px;
  background-color: #141d27;
  border-radius: 50%;
}
#cart .cartLeft .cartImg {
  display: inline-block;
  z-index: 99999;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  margin: 6px 7px 8px;
  background: rgba(255,255,255,0.2);
  font-size: 24px;
  line-height: 24px;
  text-align: center;
  padding-top: 12px;
}
#cart .cartLeft .cartImg::before {
  color: rgba(255,255,255,0.4);
}
#cart .figure {
  width: 50px;
  height: 24px;
  line-height: 24px;
  display: inline-block;
  text-align: center;
  color: rgba(255,255,255,0.4);
  margin-left: 64px;
  margin-top: 10px;
  font-size: 14px;
  font-weight: 700;
}
#cart .shippingFee {
  line-height: 24px;
  display: inline-block;
  text-align: center;
  color: rgba(255,255,255,0.4);
  margin-top: 10px;
  font-size: 14px;
}
#cart .line_color {
  color: rgba(255,255,255,0.4);
  height: 25px;
  margin-right: 6px;
  display: inline-block;
  border-left: 1px solid rgba(255,255,255,0.1);
}
#cart .allMoney {
  display: inline-block;
  font-size: 14px;
  height: 100%;
  width: 90px;
  text-align: center;
  float: right;
  background-color: rgba(255,255,255,0.1);
  color: rgba(255,255,255,0.4);
  line-height: 1;
  font-weight: 500;
  line-height: 24px;
  padding-top: 10px;
}
#cart .colorMoney {
  background-color: #4fd566;
  color: #fff;
}


