
.goods {
  width: 100%;
  height: 100%;
  padding-bottom: 44px;
}
.goods .sidebar {
  position: fixed;
  height: 100%;
  background-color: #f3f5f7;
  padding-bottom: 210px;
}
.goods .sidebar .sidebarList {
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  direction: rtl;
  margin-left: -16px;
}
.goods .sidebar .sidebarList .sideItem,
.goods .sidebar .sidebarList .sidertab {
  margin-left: 16px;
  display: table;
  width: 80px;
  height: 54px;
  line-height: 54px;
  background-color: #f3f5f7;
  padding: 0 10px;
}
.goods .sidebar .sidebarList .sideItem .itemName,
.goods .sidebar .sidebarList .sidertab .itemName {
  display: table-cell;
  width: 56px;
  vertical-align: middle;
  line-height: 14px;
  font-size: 12px;
  font-weight: 200;
  text-align: center;
  height: 54px;
  border-bottom: 1px solid rgba(7,17,27,0.2);
/* Retina 适配 */
/* 三倍屏 适配 */
/*  color:rgb(240,20,20)*/
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 200/100), only screen and (min-device-pixel-ratio: 2) {
.goods .sidebar .sidebarList .sideItem .itemName .itemName,
  .goods .sidebar .sidebarList .sidertab .itemName .itemName {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 2.5), only screen and (min--moz-device-pixel-ratio: 2.5), only screen and (-o-min-device-pixel-ratio: 250/100), only screen and (min-device-pixel-ratio: 2.5) {
.goods .sidebar .sidebarList .sideItem .itemName .itemName,
  .goods .sidebar .sidebarList .sidertab .itemName .itemName {
    -webkit-transform: scaleY(0.33333334);
    transform: scaleY(0.33333334);
}
}
.goods .sidebar .sidebarList .sideItem .itemName .icon,
.goods .sidebar .sidebarList .sidertab .itemName .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-right: 2px;
  vertical-align: top;
}
.goods .sidebar .sidebarList .sideItem .itemName .icon.decrease,
.goods .sidebar .sidebarList .sidertab .itemName .icon.decrease {
  background: url("./decrease_3@2x.png");
  background-size: contain;
}
.goods .sidebar .sidebarList .sideItem .itemName .icon.discount,
.goods .sidebar .sidebarList .sidertab .itemName .icon.discount {
  background: url("./discount_3@2x.png");
  background-size: contain;
}
.goods .sidebar .sidebarList .sideItem .itemName .icon.guarantee,
.goods .sidebar .sidebarList .sidertab .itemName .icon.guarantee {
  background: url("./guarantee_3@2x.png");
  background-size: contain;
}
.goods .sidebar .sidebarList .sideItem .itemName .icon.invoice,
.goods .sidebar .sidebarList .sidertab .itemName .icon.invoice {
  background: url("./invoice_3@2x.png");
  background-size: contain;
}
.goods .sidebar .sidebarList .sideItem .itemName .icon.special,
.goods .sidebar .sidebarList .sidertab .itemName .icon.special {
  background: url("./special_3@2x.png");
  background-size: contain;
}
.goods .sidebar .sidebarList .sidertab {
  background-color: #fff;
}
.goods .sidebar .sidebarList .sidertab .itemName {
  border: none;
}
.goods .goodsContent {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding-left: 80px;
  background-color: #fff;
  box-sizing: border-box;
}
.goods .goodsContent .goodsAllList {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  margin-right: -20px;
  margin-bottom: 160px;
}
.goods .goodsContent .goodsAllList .goodsList {
  margin-right: 10px;
}
.goods .goodsContent .goodsAllList .goodsList .tittle {
  background-color: #f3f5f7;
  border-left: 2px solid #d9dde1;
  height: 26px;
  padding-left: 14px;
}
.goods .goodsContent .goodsAllList .goodsList .tittle h1 {
  line-height: 26px;
  color: #93999f;
  font-size: 12px;
}
.goods .goodsContent .goodsAllList .goodsList .content {
  margin: 18px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7,17,27,0.1);
  position: relative;
/* Retina 适配 */
/* 三倍屏 适配 */
}
.goods .goodsContent .goodsAllList .goodsList .content:last-child {
  padding-bottom: 0px;
  border: none;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min--moz-device-pixel-ratio: 2), only screen and (-o-min-device-pixel-ratio: 200/100), only screen and (min-device-pixel-ratio: 2) {
.goods .goodsContent .goodsAllList .goodsList .content .content {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@media only screen and (-webkit-min-device-pixel-ratio: 2.5), only screen and (min--moz-device-pixel-ratio: 2.5), only screen and (-o-min-device-pixel-ratio: 250/100), only screen and (min-device-pixel-ratio: 2.5) {
.goods .goodsContent .goodsAllList .goodsList .content .content {
    -webkit-transform: scaleY(0.33333334);
    transform: scaleY(0.33333334);
}
}
.goods .goodsContent .goodsAllList .goodsList .content .icon {
  width: 57px;
  height: 57px;
  margin-right: 10px;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .menuName {
  font-size: 14px;
  color: #07111b;
  line-height: 14px;
  margin-top: 2px;
  margin-bottom: 6px;
  overflow: hidden;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .disc,
.goods .goodsContent .goodsAllList .goodsList .content .menu .sellcount span {
  font-size: 10px;
  color: #93999f;
  line-height: 14px;
  margin-bottom: 8px;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .sellcount {
  overflow: hidden;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .sellcount span:first-child {
  margin-right: 12px;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .price {
  display: inline-block;
  height: 24px;
  width: 100%;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .price .priceNow {
  font-size: 10px;
  font-weight: 700;
  line-height: 24px;
  color: #f01414;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .price .priceOld {
  font-size: 10px;
  font-weight: 700;
  line-height: 24px;
  margin-left: 8px;
  text-decoration: line-through;
  color: #93999f;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .price .cart {
  position: absolute;
  width: 82px;
  height: 22px;
  z-index: 200;
  display: inline-block;
  right: 0px;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .price .cart .cart_add {
  float: right;
  height: 24px;
  width: 24px;
  display: inline-block;
  vertical-align: top;
  font-size: 24px;
  color: #00a0dc;
  line-height: 24px;
  overflow: hidden;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .price .cart .cart_add span {
  display: block;
}
.goods .goodsContent .goodsAllList .goodsList .content .menu .price .cart .add_count {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  color: #93999f;
  line-height: 24px;
  text-align: center;
}


